import WxValidate from '../../utils/WxValidate.js'
import uniPopup from "../../components/uni-popup/uni-popup.vue"
import {
	LOGIN,
	SENDCODE,
	MYINFO
} from "../../utils/api.js"
var curToken
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			loginData: [{
					title: '50+',
					titleTip: '场景',
					text1: '陌生电话放心挂断',
					text2: '大数据识别，骚扰电话无处遁形',
					iconGroup: [{
							img: '../../static/loginIntroduce/baoxiantuixiao@2x.png',
							name: '保险推销'
						},
						{
							img: '../../static/loginIntroduce/fangchan@2x.png',
							name: '房产中介'
						},
						{
							img: '../../static/loginIntroduce/licai@2x.png',
							name: '贷款理财'
						},
						{
							img: '../../static/loginIntroduce/cuishou@2x.png',
							name: '催收骚扰'
						}
					]
				},
				{
					title: '20+',
					titleTip: '情景',
					text1: '生活服务电话智能代接',
					text2: '“放门口”、“放快递柜”、“定位准确”',
					iconGroup: [{
							img: '../../static/loginIntroduce/waimai@2x.png',
							name: '外卖'
						},
						{
							img: '../../static/loginIntroduce/kuaidi@2x.png',
							name: '快递'
						},
						{
							img: '../../static/loginIntroduce/dache@2x.png',
							name: '打车'
						}
					]
				},
				{
					title: '40+',
					titleTip: '行业',
					text1: '飞行模式/无服务也不漏接',
					text2: '安心放心，重要来电不错过、不遗漏',
					iconGroup: [{
							img: '../../static/loginIntroduce/kaihui@2x.png',
							name: '开会'
						},
						{
							img: '../../static/loginIntroduce/xiuxi@2x.png',
							name: '休息'
						},
						{
							img: '../../static/loginIntroduce/guanji@2x.png',
							name: '关机'
						}
					]
				}
			],
			mobileCode: "获取验证码",
			time: 60,
			mobileTip: "",
			inputValue: "",
			smsTip: "",
			mobile: "",
			smsCode: "",
			isDisable: false,
			bindmobile: '',
			isCheck: false,
			checked: 'agree',
			vip: ''

		}
	},
	onLoad() {
		this.initValidate();
		this.getUrlToken();
	},
	methods: {
		userAgree(){
			uni.navigateTo({
				url:'../../static/privacyAgreement/privacyAgreement'
			})
		},
		privacyAgree(){
			uni.navigateTo({
				url:'../../static/userAgreement/userAgreement'
			})
		},
		// 截取
		getQueryString(name) {
			var after = window.location.search
			if (after.indexOf('?') === -1) {}
			after = window.location.href.split("?")[1] || after.substr(1);
			if (after) {
				var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
				var r = after.match(reg)
				if (r !== null) {
					return decodeURIComponent(r[2]);
				} else {
					return null
				}
			}
		},
		// 获取当前保存token
		getUrlToken() {
			curToken = this.getQueryString('token')
			console.log('绑定手机中的token',curToken)
			if(!curToken) 
			{
				//localstorage read
				curToken = uni.getStorageSync('myToken')
				console.log('locattion',curToken)
			}
			
			if(!curToken) {
				console.log('路径和storage的token都为空')
				uni.navigateTo({
					url:"../JumpLogin/JumpLogin"
				})
			} else {
				try{
					uni.setStorageSync('myToken',curToken)
				}catch(e){
					//TODO handle the exception
					uni.showModal({
						content: e,
						showCancel: false
					});
				}
			}	
			
		},
		// 规则校验
		initValidate() {
			// 规则
			const rules = {
				mobile: {
					required: true,
					tel: true
				},
				smsCode: {
					required: true
				}
			}
			// 提示
			const messages = {
				mobile: {
					required: "请输入手机号！"
				},
				smsCode: {
					required: "请输入验证码"
				}
			}
			this.WxValidate = new WxValidate(rules, messages)
		},
		// 获取验证码
		getMobileCode() {
			if (this.bindmobile) {
				if (this.mobileCode == '获取验证码') {
					this.time = 60
					this.timer()
					var jiamiMobile = this.$encruption(this.bindmobile)
					const params = {
						mobile:this.bindmobile,
						elmobi: jiamiMobile
					}
					console.log(params)
					this.$request.url_request(SENDCODE, params, 'POST', res => {
						uni.showToast({
							title: '验证码发送成功',
							icon: 'none',
							duration: 1000
						})
					}, err => {})
				}
			} else {
				uni.showToast({
					title: '手机号不可为空',
					icon: 'none',
					duration: 1000
				})
			}
		
		
		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.mobileCode = "还剩" + this.time + "s"
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.mobileCode = "获取验证码";
			}
		},
		// 登录
		handleLogin(e) {
			var that = this
			this.isDisable = true
			setTimeout(function() {
				that.isDisable = false
				that.$forceUpdate()
			}, 1000)
			// const params = e.detail.value
			const params = e.detail.value
			if (!this.WxValidate.checkForm(params)) {
				const error = this.WxValidate.errorList[0]
				// 可以自定义提示
				uni.showToast({
					title: `${error.msg}`,
					icon: 'none'
				});
				return false
			} else {
				if(this.isCheck == false){
					uni.showToast({
						title:'请阅读隐私协议',
						icon:'none',
						duration:1200
					})
				} else{
				var _this = this
				this.$request.url_request(LOGIN, params, "POST", res => {
					if (JSON.parse(res.data).code == 0) {
						const params1 = {}
						_this.$request.url_request(MYINFO,params1,'GET',res=>{
							_this.vip = JSON.parse(res.data).data.vip
							if(_this.vip){
								uni.reLaunch({
									url:'../IncomeHome/IncomeHome'
								})
							} else{
								uni.navigateTo({
									url: '../Welcome/Welcome'
								})
							}			
						},err=>{})
					} else {
						if (JSON.parse(res.data).msg) {
							uni.showToast({
								title: JSON.parse(res.data).msg,
								icon: 'none',
								duration: 1000
							})
						}
					}
				}, err => {
		
				});
				}
				
			}
		},
		tonghuashili(){
			uni.navigateTo({
				url:'callList/callList'
			})
		},
		open() {
			this.$refs.popup.open()
		},
		checkboxChange(value) {
			this.isCheck = !this.isCheck
		},
		// 收起键盘
		pickupKeyboard() {
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			})
		}
	}
}
